import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import SeoHelmet from './SeoHelmet';

const MenuContentsItem = props => {
  const seo = {
    title: `${props.item.name} | Seoul Food Take-Out | Korean Food`,
    description: `${props.item.description}`,
    canonical: `https://seoulfoodtakeout.com/${props.path}/${props.item.name
      .toLowerCase()
      .replace(',', '')
      .split(' ')
      .join('-')}`,
    og: {
      locale: 'en_CA',
      title: `${props.item.name} | Seoul Food Take-Out | Korean Food`,
      image: `https://seoulfoodtakeout.com${props.item.image}`,
      url: `https://seoulfoodtakeout.com/${props.path}/${props.item.name
        .toLowerCase()
        .replace(',', '')
        .split(' ')
        .join('-')}`,
      description: `${props.item.description}`
    }
  };
  const handleClick = () => {
    // props.history.goBack();
    let array = props.history.location.pathname.split('/');
    array.pop();
    array = array.join('/');
    props.history.push(array);
  };
  return (
    <>
      <SeoHelmet head={seo} />
      <div className="popup-box" onClick={handleClick}>
        <button className="close">&times;</button>
        <div className="contentBox" onClick={e => e.stopPropagation()}>
          {props.titleDesc && <h5 className="titleDesc">{props.titleDesc}</h5>}
          <h1 className="title">
            {props.item.name}
            {/* <span className="priceBox">
            <span className="priceDollar">$</span>
            <span className="price">{props.item.price}</span>
          </span> */}
          </h1>
          <div className="img-box">
            <img
              src={props.item.largeImage}
              alt={props.item.name}
              title={props.item.name}
            />
          </div>
          <p className="desc">{props.item.description}</p>
          <span className="priceBox">
            <span className="priceDollar">$</span>
            <span className="price">{props.item.price}</span>
          </span>
        </div>
      </div>
    </>
  );
};
MenuContentsItem.propTypes = {
  item: PropTypes.object.isRequired
};

export default withRouter(MenuContentsItem);
