import React, { Component } from 'react';

import StoreHours from '../components/StoreHours';
import StoreInfo from '../components/StoreInfo';
import GoogleMap from '../components/GoogleMap';
import ContextType from '../helper/ContextType';
import SeoHelmet from '../components/SeoHelmet';
import SeoData from '../data/SeoData';

class Contact extends Component {
  static contextType = ContextType;
  componentDidMount() {
    this.props.resetTop(false);
    this.context.singleItem = false;
  }
  render() {
    return (
      <>
        <SeoHelmet head={SeoData.contact} />
        <div className="header contactHeader" />
        <div className="seoulContainer info-box">
          <h2>
            Seoul Food Take-Out is conveniently located across Sherbourne
            station
          </h2>
          <div className="info-items">
            <StoreHours />
            <StoreInfo />
          </div>
        </div>
        <div className="seoulContainer map-box">
          <div className="buttonBox">
            <a
              href="https://www.google.com/maps/place/Seoul+Food+Take-Out/@43.6719658,-79.3768762,15z/data=!4m5!3m4!1s0x0:0xe57c456e3a4c6ee4!8m2!3d43.6719658!4d-79.3768762"
              target="blank"
              title="click for a larger map in a separate window"
            >
              View Larger Map
            </a>
          </div>
          <GoogleMap />
        </div>
      </>
    );
  }
}

export default Contact;
