import React, { Component } from 'react';
import { Link, Switch, Route } from 'react-router-dom';
import PropTypes from 'prop-types';
import $ from 'jquery';

import MenuContentsItem from './MenuContentsItem';
import ContextType from '../helper/ContextType';
import SeoHelmet from './SeoHelmet';

class MenuContents extends Component {
  static contextType = ContextType;
  static propTypes = {
    data: PropTypes.object.isRequired,
    isTouch: PropTypes.bool.isRequired
  };
  componentDidMount() {
    const menuHeader = document.getElementsByClassName('menuHeader');
    // if (this.props.history.location.pathname.includes('menu')) {
    //   document.getElementsByClassName('menuLink')[0].classList.add('active');
    // }
    if (this.props.location.pathname.includes('menu')) {
      document.getElementsByClassName('menuLink')[0].classList.add('active');
    }
    const scrollTo = menuHeader[0].clientHeight - 120;
    if (this.context.singleItem) {
      $('.wrapper').animate({ scrollTop: this.context.currentScrollTop }, 0);
    } else {
      if (this.props.fixed) {
        $('.wrapper').animate({ scrollTop: scrollTo }, 0);
      }
    }
    // if (this.state.subRendered) {
    //   $('.wrapper').animate({ scrollTop: $('.wrapper').scrollTop() }, 0);
    // } else {
    //   if (this.props.fixed) {
    //     $('.wrapper').animate({ scrollTop: scrollTo }, 0);
    //   }
    // }
  }
  // componentWillUnmount() {
  //   if (this.props.location.pathname.includes('menu')) {
  //     document.getElementsByClassName('menuLink')[0].classList.remove('active');
  //   }
  // }
  handleSingleItemClick = e => {
    // this.setState({ subRendered: false });
    this.context.singleItem = true;
    this.context.currentScrollTop = $('.wrapper').scrollTop();
  };
  shouldComponentUpdate(nextProps, nextState) {
    return false;
    // return nextProps.data !== this.props.data;
  }
  render() {
    const { data } = this.props;
    const dataWithImage = data.data.filter(item => item.image !== '');
    const dataWithoutImage = data.data.filter(item => item.image === '');
    // const seo = {
    //   title: `${props.item.name} | Seoul Food Take-Out | Korean Food`,
    //   description: `${props.item.description}`,
    //   canonical: `https://seoulfoodtakeout.com${props.item.url}`,
    //   og: {
    //     locale: 'en_CA',
    //     title: `${props.item.name} | Seoul Food Take-Out | Korean Food`,
    //     image: `https://seoulfoodtakeout.com${props.item.image}`,
    //     url: `https://seoulfoodtakeout.com${props.item.url}`,
    //     description: `${props.item.description}`
    //   }
    // };
    // console.log(data);
    return (
      <>
        <SeoHelmet head={data.seo} />
        <div className="menuContentBox">
          <h1 className="title">{data.title}</h1>
          <div className="cashOnly">*Sorry, only cash is accepted</div>
          <div className="rose-hr-box">
            <img
              className="rose-hr"
              src="/images/rose-hr-260.jpg"
              alt="rose hr"
            />
          </div>
          {data.description && <p className="titleDesc">{data.description}</p>}
          <div className="itemsBox">
            <div className="itemBoxGrid">
              {dataWithImage.length > 0 &&
                dataWithImage.map((item, i) => (
                  <div className="itemBox" key={item.id}>
                    <div className="imageBox">
                      <Link
                        to={`${data.path}/${item.name
                          .toLowerCase()
                          .replace(',', '')
                          .split(' ')
                          .join('-')}`}
                        onClick={this.handleSingleItemClick}
                      >
                        <img
                          src={item.image}
                          alt={item.name}
                          title={item.name}
                        />
                      </Link>
                      <Switch>
                        <Route
                          key={`${item.id}-${item.name.split(' ').join('')}`}
                          // exact
                          path={`${data.path}/${item.name
                            .toLowerCase()
                            .replace(',', '')
                            .split(' ')
                            .join('-')}`}
                          render={() => (
                            <MenuContentsItem
                              item={item}
                              titleDesc={data.description}
                              path={data.path}
                            />
                          )}
                        />
                        <Route
                          render={() => (
                            <h1 style={{ textAlign: 'center' }}>
                              Oops! That menu item doesn't exist!
                            </h1>
                          )}
                        />
                      </Switch>
                    </div>

                    <div className="contentBox">
                      <div className="name">
                        {item.name}

                        {item.nameDescription && (
                          <span className="nameDescription">
                            {item.nameDescription}
                          </span>
                        )}
                        {item.day && <span className="day">{item.day}</span>}
                      </div>
                      {item.description2 && (
                        <div className="description2">{item.description2}</div>
                      )}
                      <div className="description">{item.description}</div>
                      <div className="price">
                        {item.regularPrice && (
                          <>
                            <span className="striked">{item.regularPrice}</span>
                            <span>&nbsp;&nbsp;</span>
                          </>
                        )}
                        {!item.regularPrice && (
                          <span className="dollar-sign">$</span>
                        )}
                        {item.price}
                      </div>
                    </div>
                  </div>
                ))}
              {dataWithoutImage.length > 0 &&
                dataWithoutImage.map((item, i) => (
                  <div className="itemBox" key={item.id}>
                    <div className="imageBox">
                      <Link
                        to={`${data.path}/${item.name
                          .toLowerCase()
                          .replace(',', '')
                          .split(' ')
                          .join('-')}`}
                      >
                        {/* <img src={item.image} alt={item.name} title={item.name} /> */}
                      </Link>
                      <Switch>
                        <Route
                          key={`${item.id}-${item.name.split(' ').join('')}`}
                          exact
                          path={`${data.path}/${item.name
                            .toLowerCase()
                            .replace(',', '')
                            .split(' ')
                            .join('-')}`}
                          render={() => (
                            <MenuContentsItem item={item} path={data.path} />
                          )}
                        />
                      </Switch>
                    </div>

                    <div className="contentBox">
                      <div className="name">
                        {item.name}

                        {item.nameDescription && (
                          <span className="nameDescription">
                            {item.nameDescription}
                          </span>
                        )}
                        {item.day && <span className="day">{item.day}</span>}
                      </div>
                      {item.description2 && (
                        <div className="description2">{item.description2}</div>
                      )}
                      <div className="description">{item.description}</div>
                      <div className="price">
                        {item.regularPrice && (
                          <>
                            <span className="striked">{item.regularPrice}</span>
                            <span>&nbsp;&nbsp;</span>
                          </>
                        )}
                        {!item.regularPrice && (
                          <span className="dollar-sign">$</span>
                        )}
                        {item.price}
                      </div>
                    </div>
                  </div>
                ))}
            </div>
          </div>

          {/* <div>
          {Array.apply(null, Array(50)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
        </div>
      </>
    );
  }
}

export default MenuContents;
